/* src/styles/DashboardPage.css */

/* –û–±—â–∏–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å—Ç—Ä–∞–Ω–∏—Ü—ã (–±–∞–∑–æ–≤—ã–µ —Å—Ç–∏–ª–∏ –≤ index.css) */
.dashboard-page {
    /* –ú–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å —Å–ø–µ—Ü–∏—Ñ–∏—á–Ω—ã–µ –æ—Ç—Å—Ç—É–ø—ã, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ */
}

/* –ë–ª–æ–∫ –ø—Ä–∏–≤–µ—Ç—Å—Ç–≤–∏—è */
.user-greeting,
.guest-greeting {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding-bottom: 15px;
    border-bottom: 1px solid var(--accent-color);
    transition: border-color var(--transition-speed) ease;
}
.user-greeting p,
.guest-greeting p {
    margin: 0;
    color: var(--text-primary);
}

/* –°–µ–∫—Ü–∏—è –ø—Ä–æ–µ–∫—Ç–æ–≤ */
.projects-section {
    margin-top: 30px;
}
.projects-section h2 {
    margin-bottom: 20px;
}


/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —Ñ–∏–ª—å—Ç—Ä–∞ */
.filter-container {
    margin-bottom: 20px;
}
.filter-container label {
    margin-right: 10px;
    color: var(--text-secondary);
    font-weight: 500;
}
/* –°—Ç–∏–ª–∏ –¥–ª—è form-input —É–∂–µ –µ—Å—Ç—å –≤ index.css */


/* –°–ø–∏—Å–æ–∫ –ø—Ä–æ–µ–∫—Ç–æ–≤ */
.project-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

/* –≠–ª–µ–º–µ–Ω—Ç —Å–ø–∏—Å–∫–∞ –ø—Ä–æ–µ–∫—Ç–æ–≤ */
.project-list-item {
    display: flex;
    justify-content: space-between; /* –†–∞–∑–Ω–æ—Å–∏—Ç –Ω–∞–∑–≤–∞–Ω–∏–µ –∏ –∫–Ω–æ–ø–∫—É */
    align-items: center;
    padding: 15px 20px;
    margin-bottom: 10px;
    background-color: var(--card-bg); /* –§–æ–Ω –∫–∞—Ä—Ç–æ—á–∫–∏ */
    border: 1px solid var(--accent-color); /* –†–∞–º–∫–∞ */
    border-radius: 6px;
    box-shadow: var(--shadow-sm); /* –õ–µ–≥–∫–∞—è —Ç–µ–Ω—å */
    transition: background-color var(--transition-speed) ease,
    border-color var(--transition-speed) ease,
    box-shadow 0.2s ease,
    opacity 0.3s ease; /* –ü–ª–∞–≤–Ω–æ—Å—Ç—å –¥–ª—è —É–¥–∞–ª–µ–Ω–∏—è */
}

.project-list-item:hover {
    box-shadow: var(--shadow-md); /* –¢–µ–Ω—å –ø–æ–±–æ–ª—å—à–µ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

/* –°—Ç–∏–ª–∏–∑–∞—Ü–∏—è —Å—Å—ã–ª–∫–∏ –≤–Ω—É—Ç—Ä–∏ —ç–ª–µ–º–µ–Ω—Ç–∞ —Å–ø–∏—Å–∫–∞ */
.project-list-item a {
    color: var(--text-primary);
    text-decoration: none;
    font-weight: 500;
    flex-grow: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –¥–æ—Å—Ç—É–ø–Ω–æ–µ –º–µ—Å—Ç–æ */
    margin-right: 15px; /* –û—Ç—Å—Ç—É–ø –æ—Ç –∫–Ω–æ–ø–∫–∏ */
}
.project-list-item a:hover {
    text-decoration: underline;
    opacity: 1; /* –£–±–∏—Ä–∞–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–π hover –¥–ª—è —Å—Å—ã–ª–æ–∫ */
}

/* –°—Ç–∏–ª—å –¥–ª—è —ç–ª–µ–º–µ–Ω—Ç–∞ –≤–æ –≤—Ä–µ–º—è —É–¥–∞–ª–µ–Ω–∏—è */
.project-list-item--deleting {
    opacity: 0.5;
    pointer-events: none; /* –ë–ª–æ–∫–∏—Ä—É–µ–º –≤–∑–∞–∏–º–æ–¥–µ–π—Å—Ç–≤–∏–µ */
}

/* –ö–Ω–æ–ø–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –≤–Ω—É—Ç—Ä–∏ —Å–ø–∏—Å–∫–∞ */
.project-list-item .button--delete {
    padding: 2px 8px; /* –î–µ–ª–∞–µ–º –∫–Ω–æ–ø–∫—É –ø–æ–º–µ–Ω—å—à–µ */
    font-size: 0.9em;
    background-color: transparent;
    border: 1px solid transparent; /* –£–±–∏—Ä–∞–µ–º —Ä–∞–º–∫—É */
    color: #f44336; /* –ö—Ä–∞—Å–Ω—ã–π —Ü–≤–µ—Ç */
    opacity: 0.7; /* –ß—É—Ç—å –ø—Ä–æ–∑—Ä–∞—á–Ω–µ–µ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é */
}
body.dark-theme .project-list-item .button--delete {
    color: #ef9a9a; /* –°–≤–µ—Ç–ª–æ-–∫—Ä–∞—Å–Ω—ã–π –Ω–∞ —Ç–µ–º–Ω–æ–º */
}
.project-list-item .button--delete:hover:not(:disabled) {
    opacity: 1; /* –ü–æ–ª–Ω–∞—è –Ω–µ–ø—Ä–æ–∑—Ä–∞—á–Ω–æ—Å—Ç—å –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
    background-color: rgba(244, 67, 54, 0.1); /* –õ–µ–≥–∫–∏–π –∫—Ä–∞—Å–Ω—ã–π —Ñ–æ–Ω */
}
body.dark-theme .project-list-item .button--delete:hover:not(:disabled) {
    background-color: rgba(239, 154, 154, 0.15);
}


/* –ö–Ω–æ–ø–∫–∞ "–°–æ–∑–¥–∞—Ç—å –ø—Ä–æ–µ–∫—Ç" */
.button--create-project {
    margin-top: 25px;
    font-weight: 500;
}