/* src/styles/SettingsPage.css */

/* –û–±—â–∏–π –∫–ª–∞—Å—Å page-container –ø—Ä–µ–¥–ø–æ–ª–∞–≥–∞–µ—Ç—Å—è –≤ index.css/App.css
   —Å max-width (–Ω–∞–ø—Ä–∏–º–µ—Ä, 1500px) –∏ margin: auto */

.settings-page h1 {
    text-align: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –≥–ª–∞–≤–Ω—ã–π –∑–∞–≥–æ–ª–æ–≤–æ–∫ —Å—Ç—Ä–∞–Ω–∏—Ü—ã */
    margin-bottom: 40px; /* –û—Ç—Å—Ç—É–ø —Å–Ω–∏–∑—É */
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Å–µ–∫—Ü–∏–π –Ω–∞—Å—Ç—Ä–æ–µ–∫ */
.settings-section {
    /* --- –£–ú–ï–ù–¨–®–ï–ù–ò–ï –®–ò–†–ò–ù–´ –ò –¶–ï–ù–¢–†–ò–†–û–í–ê–ù–ò–ï --- */
    max-width: 650px;      /* <<< –ó–∞–¥–∞–µ–º –º–∞–∫—Å–∏–º–∞–ª—å–Ω—É—é —à–∏—Ä–∏–Ω—É –±–ª–æ–∫–∞ —Å–µ–∫—Ü–∏–∏ */
    width: 100%;           /* –ó–∞–Ω–∏–º–∞–µ—Ç –¥–æ—Å—Ç—É–ø–Ω—É—é —à–∏—Ä–∏–Ω—É, –Ω–æ –Ω–µ –±–æ–ª–µ–µ max-width */
    margin-left: auto;     /* <<< –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º —Å–∞–º –±–ª–æ–∫ —Å–µ–∫—Ü–∏–∏ */
    margin-right: auto;    /* <<< –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º —Å–∞–º –±–ª–æ–∫ —Å–µ–∫—Ü–∏–∏ */
    text-align: center;    /* <<< –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –í–ï–°–¨ —Ç–µ–∫—Å—Ç –≤–Ω—É—Ç—Ä–∏ —Å–µ–∫—Ü–∏–∏ */
    /* --- */
    margin-bottom: 30px;   /* –û—Ç—Å—Ç—É–ø –º–µ–∂–¥—É —Å–µ–∫—Ü–∏—è–º–∏ */
    padding: 25px 30px;    /* –í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ –æ—Ç—Å—Ç—É–ø—ã */
    background-color: var(--card-bg);
    border-radius: 8px;
    box-shadow: var(--shadow-md);
    border: 1px solid var(--accent-color);
    transition: background-color var(--transition-speed) ease, border-color var(--transition-speed) ease;
}

.settings-section h2 {
    margin-top: 0;
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--accent-color);
    font-size: 1.3em;
    font-weight: 600;
    /* text-align: center; - —É–Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–æ */
    transition: border-color var(--transition-speed) ease;
}

.settings-section p {
    margin-top: 0;
    margin-bottom: 20px;
    color: var(--text-secondary);
    /* text-align: center; - —É–Ω–∞—Å–ª–µ–¥–æ–≤–∞–Ω–æ */
}

.settings-section p strong {
    color: var(--text-primary);
    font-weight: 600;
}

/* –ö–Ω–æ–ø–∫–∏ –≤–Ω—É—Ç—Ä–∏ —Å–µ–∫—Ü–∏–π */
.settings-section .button {
    margin-top: 10px;
    /* –ß—Ç–æ–±—ã –∫–Ω–æ–ø–∫–∏ –Ω–µ —Ä–∞—Å—Ç—è–≥–∏–≤–∞–ª–∏—Å—å –Ω–∞ –≤—Å—é —à–∏—Ä–∏–Ω—É –∏–∑-–∑–∞ text-align: center */
    display: inline-block;
    width: auto; /* –®–∏—Ä–∏–Ω–∞ –ø–æ –∫–æ–Ω—Ç–µ–Ω—Ç—É + padding */
    min-width: 160px; /* –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –µ–¥–∏–Ω–æ–æ–±—Ä–∞–∑–∏—è */
}

/* –ö–Ω–æ–ø–∫–∞ –í—ã–π—Ç–∏ */
.button--danger {
    background-color: #d32f2f; border-color: #d32f2f; color: white;
}
.button--danger:hover:not(:disabled) { background-color: #c62828; border-color: #c62828; opacity: 1;}
body.dark-theme .button--danger { background-color: #e57373; border-color: #e57373; color: #1a1a1a; }
body.dark-theme .button--danger:hover:not(:disabled) { background-color: #ef9a9a; border-color: #ef9a9a; }


/* –ë–ª–æ–∫ –≤—ã–±–æ—Ä–∞ —è–∑—ã–∫–∞ */
.language-selector {
    /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º —Å–∞–º –±–ª–æ–∫ –≤–Ω—É—Ç—Ä–∏ settings-section */
    display: inline-block; /* –ß—Ç–æ–±—ã –±–ª–æ–∫ –Ω–µ —Ä–∞—Å—Ç—è–≥–∏–≤–∞–ª—Å—è */
    margin-top: 10px;
    /* –í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç—ã –±—É–¥—É—Ç —Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã –∏–∑-–∑–∞ text-align: center —É —Ä–æ–¥–∏—Ç–µ–ª—è */
}

.language-selector label {
    /* –ú–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å –æ—Ç—Å—Ç—É–ø —Å–ø—Ä–∞–≤–∞, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ */
    margin-right: 8px;
    color: var(--text-primary);
    font-weight: 500;
}

/* Select */
.language-selector .form-input {
    min-width: 150px;
    width: auto;
    padding: 6px 10px;
    vertical-align: middle; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ —Å —Ç–µ–∫—Å—Ç–æ–º label */
    text-align: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –≤–Ω—É—Ç—Ä–∏ select */
    /* text-align-last: center; */ /* –î–ª—è –Ω–µ–∫–æ—Ç–æ—Ä—ã—Ö –±—Ä–∞—É–∑–µ—Ä–æ–≤ */
}
/* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –æ–ø—Ü–∏–π */
.language-selector .form-input option{
    text-align: left; /* –û–ø—Ü–∏–∏ –ª—É—á—à–µ –æ—Å—Ç–∞–≤–∏—Ç—å —Å–ª–µ–≤–∞ */
}