/* src/styles/Navbar.css */

.navbar {
    /* –£–±—Ä–∞–ª–∏ background-color, –æ–Ω –±—É–¥–µ—Ç –æ—Ç body */
    /* –£–±—Ä–∞–ª–∏ position: sticky –∏ top: 0 */
    /* –£–±—Ä–∞–ª–∏ padding: 15px 0; */
    border-bottom: 1px solid var(--accent-color); /* –ù–∏–∂–Ω—è—è –≥—Ä–∞–Ω–∏—Ü–∞ –æ—Å—Ç–∞–µ—Ç—Å—è */
    transition: border-color var(--transition-speed) ease;

    /* --- –ù–û–í–´–ï –°–¢–ò–õ–ò (–ø–µ—Ä–µ–Ω–µ—Å–µ–Ω—ã —Å page-container/navbar-container) --- */
    display: flex;                  /* –í–∫–ª—é—á–∞–µ–º Flexbox */
    justify-content: space-between; /* –õ–æ–≥–æ —Å–ª–µ–≤–∞, —Å—Å—ã–ª–∫–∏ —Å–ø—Ä–∞–≤–∞ */
    align-items: center;            /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–µ–º –ø–æ —Ü–µ–Ω—Ç—Ä—É –≤–µ—Ä—Ç–∏–∫–∞–ª–∏ */
    max-width: 1200px;              /* –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ (–∫–∞–∫ —É page-container) */
    margin: 0 auto;                 /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –Ω–∞–≤–±–∞—Ä */
    padding: 15px 20px;             /* –û—Ç—Å—Ç—É–ø—ã –ø–æ –±–æ–∫–∞–º –∏ —Å–≤–µ—Ä—Ö—É/—Å–Ω–∏–∑—É */
    /* --- –ö–û–ù–ï–¶ –ù–û–í–´–• –°–¢–ò–õ–ï–ô --- */
}

/* –°–µ–ª–µ–∫—Ç–æ—Ä .navbar-container –±–æ–ª—å—à–µ –Ω–µ –Ω—É–∂–µ–Ω */
/* .navbar-container { ... } */

.navbar-logo {
    font-size: 1.4em; /* –ú–æ–∂–Ω–æ —Å–¥–µ–ª–∞—Ç—å —á—É—Ç—å –º–µ–Ω—å—à–µ, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ */
    font-weight: 600;
    color: var(--text-primary);
    text-decoration: none;
    flex-shrink: 0; /* –ù–µ —Å–∂–∏–º–∞—Ç—å—Å—è */
}
.navbar-logo:hover { opacity: 0.8; }

.navbar-links {
    display: flex;
    align-items: center;
    gap: 20px; /* –£–º–µ–Ω—å—à–∏–º —Ä–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É —Å—Å—ã–ª–∫–∞–º–∏ */
}

.nav-link {
    color: var(--text-secondary);
    text-decoration: none;
    font-weight: 500;
    padding: 5px 0; /* –£–±–∏—Ä–∞–µ–º –±–æ–∫–æ–≤—ã–µ –æ—Ç—Å—Ç—É–ø—ã —É —Å–∞–º–∏—Ö —Å—Å—ã–ª–æ–∫ */
    position: relative;
    transition: color var(--transition-speed) ease;
    border: none;
    background: none;
    cursor: pointer;
    font-size: 0.95em; /* –ß—É—Ç—å –º–µ–Ω—å—à–µ */
    font-family: inherit;
}

.nav-link:hover { color: var(--text-primary); }
.nav-link--active { color: var(--text-primary); font-weight: 600; }
/* –£–±–∏—Ä–∞–µ–º –ø–æ–¥—á–µ—Ä–∫–∏–≤–∞–Ω–∏–µ –∞–∫—Ç–∏–≤–Ω–æ–π —Å—Å—ã–ª–∫–∏ –¥–ª—è –º–∏–Ω–∏–º–∞–ª–∏–∑–º–∞ */
/* .nav-link--active::after { ... } */


/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–æ–∫-—Å—Å—ã–ª–æ–∫ (–±–µ–∑ –∏–∑–º–µ–Ω–µ–Ω–∏–π) */
.button--logout { color: #e57373; /* –ö—Ä–∞—Å–Ω—ã–π */ }
.button--logout:hover { color: #d32f2f; }
body.dark-theme .button--logout { color: #ef9a9a; }
body.dark-theme .button--logout:hover { color: #e57373; }

.button--login {
    /* –ú–æ–∂–Ω–æ –æ—Å—Ç–∞–≤–∏—Ç—å –∫–∞–∫ –µ—Å—Ç—å –∏–ª–∏ —Å—Ç–∏–ª–∏–∑–æ–≤–∞—Ç—å –æ—Å–æ–±–æ */
}
.navbar {
    opacity: 0;  /* –ù–∞—á–∞–ª—å–Ω–∞—è –ø—Ä–æ–∑—Ä–∞—á–Ω–æ—Å—Ç—å */
    transform: translateY(-20px); /* –ù–∞—á–∞–ª—å–Ω–æ–µ –ø–æ–ª–æ–∂–µ–Ω–∏–µ */
    animation: fadeInNavbar 0.5s ease-out forwards; /* –ê–Ω–∏–º–∞—Ü–∏—è */
}

/* –ö–ª—é—á–µ–≤—ã–µ –∫–∞–¥—Ä—ã –¥–ª—è –∞–Ω–∏–º–∞—Ü–∏–∏ */
@keyframes fadeInNavbar {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}