/* src/index.css */

/* --- –ë–∞–∑–æ–≤—ã–π —Å–±—Ä–æ—Å –∏ –Ω–∞—Å—Ç—Ä–æ–π–∫–∏ --- */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

* {
  box-sizing: border-box;
}

/* --- CSS –ü–µ—Ä–µ–º–µ–Ω–Ω—ã–µ –¥–ª—è –¢–µ–º --- */

/* –°–≤–µ—Ç–ª–∞—è —Ç–µ–º–∞ (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é) */
body, body.light-theme {
  /* –¶–≤–µ—Ç–∞ */
  --bg-primary: #ffffff;        /* –û—Å–Ω–æ–≤–Ω–æ–π —Ñ–æ–Ω (–±–µ–ª—ã–π) */
  --bg-secondary: #f0f0f0;      /* –í—Ç–æ—Ä–∏—á–Ω—ã–π —Ñ–æ–Ω (–æ—á–µ–Ω—å —Å–≤–µ—Ç–ª–æ-—Å–µ—Ä—ã–π, –¥–ª—è –∫–æ–ª–æ–Ω–æ–∫) */
  --card-bg: #ffffff;           /* –§–æ–Ω –∫–∞—Ä—Ç–æ—á–µ–∫ */
  --text-primary: #1a1a1a;      /* –û—Å–Ω–æ–≤–Ω–æ–π —Ç–µ–∫—Å—Ç (–ø–æ—á—Ç–∏ —á–µ—Ä–Ω—ã–π) */
  --text-secondary: #555555;    /* –í—Ç–æ—Ä–∏—á–Ω—ã–π —Ç–µ–∫—Å—Ç (—Ç–µ–º–Ω–æ-—Å–µ—Ä—ã–π) */
  --accent-color: #cccccc;      /* –ê–∫—Ü–µ–Ω—Ç/–ë–æ—Ä–¥–µ—Ä—ã (—Å–≤–µ—Ç–ª–æ-—Å–µ—Ä—ã–π) */
  --button-bg: #333333;         /* –§–æ–Ω –∫–Ω–æ–ø–∫–∏ */
  --button-text: #ffffff;       /* –¢–µ–∫—Å—Ç –∫–Ω–æ–ø–∫–∏ */
  --shadow-color-rgb: 0, 0, 0;  /* –¶–≤–µ—Ç —Ç–µ–Ω–∏ (—á–µ—Ä–Ω—ã–π) */

  /* –¢–µ–Ω–∏ */
  --shadow-sm: 0 1px 2px rgba(var(--shadow-color-rgb), 0.08);
  --shadow-md: 0 3px 6px rgba(var(--shadow-color-rgb), 0.1);
  --shadow-lg: 0 10px 20px rgba(var(--shadow-color-rgb), 0.1);

  /* –ü–µ—Ä–µ—Ö–æ–¥—ã */
  --transition-speed: 0.3s;
}

/* –¢–µ–º–Ω–∞—è —Ç–µ–º–∞ */
/* –¢–µ–º–Ω–∞—è —Ç–µ–º–∞ */
body.dark-theme {
  /* –¶–≤–µ—Ç–∞ */
  --bg-primary: #1a1a1a;        /* –û—Å–Ω–æ–≤–Ω–æ–π —Ñ–æ–Ω (–ø–æ—á—Ç–∏ —á–µ—Ä–Ω—ã–π) */
  --bg-secondary: #2a2a2a;      /* –í—Ç–æ—Ä–∏—á–Ω—ã–π —Ñ–æ–Ω (–æ—á–µ–Ω—å —Ç–µ–º–Ω–æ-—Å–µ—Ä—ã–π) */
  --card-bg: #333333;           /* –§–æ–Ω –∫–∞—Ä—Ç–æ—á–µ–∫ (—Ç–µ–º–Ω–æ-—Å–µ—Ä—ã–π) */
  --text-primary: #e0e0e0;      /* –û—Å–Ω–æ–≤–Ω–æ–π —Ç–µ–∫—Å—Ç (–æ—á–µ–Ω—å —Å–≤–µ—Ç–ª–æ-—Å–µ—Ä—ã–π) */
  --text-secondary: #aaaaaa;    /* –í—Ç–æ—Ä–∏—á–Ω—ã–π —Ç–µ–∫—Å—Ç (—Å–µ—Ä—ã–π) */
  --accent-color: #555555;      /* –ê–∫—Ü–µ–Ω—Ç/–ë–æ—Ä–¥–µ—Ä—ã (—Ç–µ–º–Ω–æ-—Å–µ—Ä—ã–π) */
  --highlight-color: #ff9800;   /* –¶–≤–µ—Ç –∫–æ–Ω—Ç—É—Ä–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
  --button-bg: #f0f0f0;         /* –§–æ–Ω –∫–Ω–æ–ø–∫–∏ */
  --button-text: #1a1a1a;       /* –¢–µ–∫—Å—Ç –∫–Ω–æ–ø–∫–∏ */
  --shadow-color-rgb: 200, 200, 200; /* –¶–≤–µ—Ç —Ç–µ–Ω–∏ (—Å–≤–µ—Ç–ª—ã–π) */

  /* –ù–æ–≤—ã–µ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ –¥–ª—è —Ç–µ–º–Ω–æ–π —Ç–µ–º—ã */
  --bg-dark: #1a1a1a;           /* –¢–µ–º–Ω—ã–π —Ñ–æ–Ω –¥–ª—è –∫–æ–ª–æ–Ω–æ–∫ */
  --card-bg-dark: #333333;      /* –¢–µ–º–Ω—ã–π —Ñ–æ–Ω –¥–ª—è –∫–∞—Ä—Ç–æ—á–µ–∫ */
  --accent-dark: #444444;       /* –¢–µ–º–Ω–∞—è –≥—Ä–∞–Ω–∏—Ü–∞ */
  --highlight-dark: #ff9800;    /* –¶–≤–µ—Ç –∫–æ–Ω—Ç—É—Ä–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ –¥–ª—è —Ç–µ–º–Ω–æ–π —Ç–µ–º—ã */

  /* –¢–µ–Ω–∏ (–º–æ–≥—É—Ç –±—ã—Ç—å –º–µ–Ω–µ–µ –∑–∞–º–µ—Ç–Ω—ã –Ω–∞ —Ç–µ–º–Ω–æ–º —Ñ–æ–Ω–µ, –º–æ–∂–Ω–æ —É—Å–∏–ª–∏—Ç—å) */
  --shadow-sm: 0 1px 2px rgba(var(--shadow-color-rgb), 0.08);
  --shadow-md: 0 3px 6px rgba(var(--shadow-color-rgb), 0.1);
  --shadow-lg: 0 10px 20px rgba(var(--shadow-color-rgb), 0.1);
}


/* --- –ü—Ä–∏–º–µ–Ω–µ–Ω–∏–µ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã—Ö –∫ –±–∞–∑–æ–≤—ã–º —ç–ª–µ–º–µ–Ω—Ç–∞–º --- */

body {
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease; /* –ü–ª–∞–≤–Ω—ã–π –ø–µ—Ä–µ—Ö–æ–¥ */
}

h1, h2, h3, h4, h5, h6 {
  color: var(--text-primary);
  transition: color var(--transition-speed) ease;
}

p {
  color: var(--text-secondary);
  transition: color var(--transition-speed) ease;
}

a {
  color: var(--text-primary); /* –ò–ª–∏ —Å–¥–µ–ª–∞—Ç—å –∞–∫—Ü–µ–Ω—Ç–Ω—ã–º? */
  /* text-decoration: none; */
  transition: color var(--transition-speed) ease;
}
body.dark-theme a {
  color: var(--text-primary);
}
a:hover {
  opacity: 0.8;
}

button.button { /* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–æ–∫ —Å –∫–ª–∞—Å—Å–æ–º button */
  background-color: var(--button-bg);
  color: var(--button-text);
  border: 1px solid var(--accent-color);
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease, opacity var(--transition-speed) ease;
}

button.button:hover:not(:disabled) {
  opacity: 0.85;
}

button.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

hr {
  border: none;
  border-top: 1px solid var(--accent-color);
  transition: border-color var(--transition-speed) ease;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –æ–±—â–∏—Ö –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–æ–≤ (–ø—Ä–∏–º–µ—Ä) */
.dashboard-page, .project-details-page, .settings-page, .login-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞ (–∏—Å–ø–æ–ª—å–∑—É–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ) */
.modal-overlay {
  position: fixed; top: 0; left: 0; right: 0; bottom: 0;
  background-color: rgba(0, 0, 0, 0.6); /* –§–æ–Ω –≤—Å–µ–≥–¥–∞ —Ç–µ–º–Ω—ã–π –ø–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π */
  display: flex; justify-content: center; align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: var(--card-bg); /* –§–æ–Ω –∑–∞–≤–∏—Å–∏—Ç –æ—Ç —Ç–µ–º—ã */
  color: var(--text-primary); /* –¢–µ–∫—Å—Ç –∑–∞–≤–∏—Å–∏—Ç –æ—Ç —Ç–µ–º—ã */
  padding: 25px 35px;
  border-radius: 8px;
  min-width: 300px;
  max-width: 500px; /* –ú–∞–∫—Å —à–∏—Ä–∏–Ω–∞ */
  width: 90%;
  box-shadow: var(--shadow-lg); /* –¢–µ–Ω—å */
  z-index: 1001;
  transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è —Ñ–æ—Ä–º –≤–Ω—É—Ç—Ä–∏ –º–æ–¥–∞–ª–∫–∏ (–ø—Ä–∏–º–µ—Ä) */
.form-field {
  margin-bottom: 15px;
}
.form-field label {
  display: block;
  margin-bottom: 5px;
  color: var(--text-secondary);
  font-size: 0.9em;
}
.form-field input[type="text"],
.form-field input[type="email"],
.form-field input[type="password"],
.form-field textarea {
  width: 100%;
  padding: 8px 10px;
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  background-color: var(--bg-primary); /* –§–æ–Ω –ø–æ–ª—è –≤–≤–æ–¥–∞ */
  color: var(--text-primary); /* –¢–µ–∫—Å—Ç –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ */
  transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease, border-color var(--transition-speed) ease;
}
.form-field textarea {
  min-height: 80px;
  resize: vertical;
}

.form-actions {
  margin-top: 20px;
  text-align: right;
}



/* –ï—Å–ª–∏ –∫–Ω–æ–ø–∫–∏ –≤–Ω—É—Ç—Ä–∏ kanban-board */
.button-container {
  margin-bottom: 20px; /* –î–æ–±–∞–≤–∏—Ç—å –æ—Ç—Å—Ç—É–ø –¥–ª—è –ø—Ä–æ—Å—Ç—Ä–∞–Ω—Å—Ç–≤–∞ */
  display: flex;
  justify-content: flex-start;
  gap: 20px;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è TaskCard (–ø—Ä–∏–º–µ—Ä—ã) */
/* –°–∞–º –∫–æ–º–ø–æ–Ω–µ–Ω—Ç TaskCard –∏–º–µ–µ—Ç –∏–Ω–ª–∞–π–Ω-—Å—Ç–∏–ª–∏, –Ω–æ –º–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å —Å—Ç–∏–ª–∏ –ø–æ –∫–ª–∞—Å—Å—É */
.task-item { /* –î–æ–ø—É—Å—Ç–∏–º, –º—ã –¥–æ–±–∞–≤–∏–º —ç—Ç–æ—Ç –∫–ª–∞—Å—Å –≤ TaskCard */
  background-color: var(--card-bg);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--accent-color);
  /* ... –¥—Ä—É–≥–∏–µ —Å—Ç–∏–ª–∏ ... */
}
.task-item:hover {
  box-shadow: var(--shadow-md);
}

/* –ö–ª–∞—Å—Å –¥–ª—è –æ—à–∏–±–∫–∏ */
.error-message {
  color: #d32f2f; /* –ö—Ä–∞—Å–Ω—ã–π —Ü–≤–µ—Ç –¥–ª—è –æ—à–∏–±–æ–∫ */
  background-color: rgba(211, 47, 47, 0.1);
  padding: 8px 12px;
  border-radius: 4px;
  margin: 10px 0;
}
body.dark-theme .error-message {
  color: #ffcdd2; /* –°–≤–µ—Ç–ª–æ-–∫—Ä–∞—Å–Ω—ã–π –Ω–∞ —Ç–µ–º–Ω–æ–º */
  background-color: rgba(255, 205, 210, 0.15);
}
/* –î–æ–±–∞–≤—å —ç—Ç–æ –≤ —Ç–≤–æ–π –æ—Å–Ω–æ–≤–Ω–æ–π CSS —Ñ–∞–π–ª */

/* –û—Å–Ω–æ–≤–Ω—ã–µ —Å—Ç–∏–ª–∏ –∫–∞–Ω–±–∞–Ω-–¥–æ—Å–∫–∏ */
.kanban-board {
  display: flex;
  justify-content: center; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –≤—Å–µ —ç–ª–µ–º–µ–Ω—Ç—ã –ø–æ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª–∏ */
  gap: 30px; /* –î–æ–±–∞–≤–ª—è–µ–º –æ—Ç—Å—Ç—É–ø—ã –º–µ–∂–¥—É –∫–æ–ª–æ–Ω–∫–∞–º–∏ */
  margin-top: 20px;
  padding: 20px;
  width: 100%;
  height: 100%; /* –†–∞—Å—Ç—è–≥–∏–≤–∞–µ–º –ø–æ –≤—ã—Å–æ—Ç–µ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä */
  overflow-x: auto; /* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª, –µ—Å–ª–∏ –∫–æ–ª–æ–Ω–æ–∫ —Å–ª–∏—à–∫–æ–º –º–Ω–æ–≥–æ */
  box-sizing: border-box;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–æ–ª–æ–Ω–æ–∫ */
.kanban-column {
  flex: 1; /* –ö–æ–ª–æ–Ω–∫–∏ –º–æ–≥—É—Ç —Ä–∞—Å—Ç—è–≥–∏–≤–∞—Ç—å—Å—è */
  min-width: 250px; /* –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –∫–æ–ª–æ–Ω–∫–∏ */
  max-width: 300px; /* –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –∫–æ–ª–æ–Ω–∫–∏ */
  padding: 10px;
  background-color: var(--bg-secondary);
  border-radius: 8px;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  transition: background-color var(--transition-speed) ease, border-color var(--transition-speed) ease;
  border: 1px dashed var(--accent-color); /* –û—Å–Ω–æ–≤–Ω–∞—è –≥—Ä–∞–Ω–∏—Ü–∞ (–ø—É–Ω–∫—Ç–∏—Ä) */
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ —Å –∫–Ω–æ–ø–∫–∞–º–∏ */
.button-container {
  display: flex;
  justify-content: flex-start; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */
  gap: 20px; /* –†–∞—Å—Å—Ç–æ—è–Ω–∏–µ –º–µ–∂–¥—É –∫–Ω–æ–ø–∫–∞–º–∏ */
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 10px;
  z-index: 10;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–æ–∫ */
.button-container button {
  padding: 8px 15px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  background-color: var(--button-bg);
  color: var(--button-text);
  border: none;
  transition: opacity 0.3s ease;
}

.button-container button:hover {
  opacity: 0.8;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–æ–ª–æ–Ω–∫–∏ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ (–¥–ª—è –∑–æ–Ω—ã –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏—è) */
.kanban-column.droppable-over {
  border: 1px dashed transparent; /* –ü—Ä–æ–∑—Ä–∞—á–Ω–∞—è –≥—Ä–∞–Ω–∏—Ü–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
  outline: 2px dashed var(--highlight-color); /* –ü—É–Ω–∫—Ç–∏—Ä–Ω—ã–π –∫–æ–Ω—Ç—É—Ä –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ –Ω–∞ –∫–æ–ª–æ–Ω–∫—É */
  outline-offset: -4px; /* –î–ª—è –æ—Ç—Å—Ç—É–ø–∞, —á—Ç–æ–±—ã –∫–æ–Ω—Ç—É—Ä –Ω–µ –ø–µ—Ä–µ–∫—Ä—ã–≤–∞–ª —Ç–µ–∫—Å—Ç */
  transition: outline-color 0.2s ease;
}

/* –ó–∞—Å—Ç–∞–≤–ª—è–µ–º –∫–æ–Ω—Ç—É—Ä –ø–ª–∞–≤–Ω–æ –º–µ–Ω—è—Ç—å—Å—è */
.kanban-column.droppable-over:hover {
  outline-color: var(--accent-color); /* –¶–≤–µ—Ç –∫–æ–Ω—Ç—É—Ä–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}

.kanban-column h3 {
  margin-top: 0;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--accent-color);
  text-align: center;
  font-weight: 600;
  transition: border-color var(--transition-speed) ease;
}

.kanban-column__empty-message {
  color: var(--text-secondary);
  font-size: 0.9em;
  text-align: center;
  margin-top: 20px;
  flex-grow: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –æ—Å—Ç–∞–≤—à–µ–µ—Å—è –º–µ—Å—Ç–æ, –µ—Å–ª–∏ –∫–æ–ª–æ–Ω–∫–∞ –ø—É—Å—Ç–∞ */
}

/* –û—Å–Ω–æ–≤–Ω–æ–π —Å—Ç–∏–ª—å –∫–∞—Ä—Ç–æ—á–∫–∏ */
.task-card {
  padding: 10px 15px;
  border: 1px solid var(--accent-color);
  margin-bottom: 8px;
  background-color: var(--card-bg);
  color: var(--text-primary);
  border-radius: 4px;
  box-shadow: var(--shadow-sm);
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease, border-color var(--transition-speed) ease, box-shadow 0.2s ease;
}

.task-card--dragging {
  box-shadow: var(--shadow-lg); /* –£—Å–∏–ª–µ–Ω–Ω–∞—è —Ç–µ–Ω—å –¥–ª—è –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–µ–º–æ–π –∫–∞—Ä—Ç–æ—á–∫–∏ */
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è */
.task-card__delete-button {
  cursor: pointer;
  border: none;
  background: none;
  font-size: 1.2em; /* –†–∞–∑–º–µ—Ä –∫—Ä–µ—Å—Ç–∏–∫–∞ */
  padding: 0 2px;
  line-height: 1;
  transition: opacity 0.2s ease;
}

/* –¶–≤–µ—Ç–∞ –¥–ª—è –∫–Ω–æ–ø–æ–∫ —É–¥–∞–ª–µ–Ω–∏—è */
.button--delete-user {
  color: #f44336; /* –ö—Ä–∞—Å–Ω—ã–π */
}
.button--delete-guest {
  color: #ffa726; /* –û—Ä–∞–Ω–∂–µ–≤—ã–π */
}

/* –¢–µ–º–Ω—ã–µ/—Å–≤–µ—Ç–ª—ã–µ —Ç–µ–º—ã */
body.dark-theme .kanban-column {
  background-color: var(--bg-dark); /* –¢–µ–º–Ω—ã–π —Ñ–æ–Ω –¥–ª—è –∫–æ–ª–æ–Ω–æ–∫ */
  border-color: var(--accent-dark); /* –¢–µ–º–Ω—ã–π —Ü–≤–µ—Ç –≥—Ä–∞–Ω–∏—Ü—ã */
}

body.dark-theme .task-card {
  background-color: var(--card-bg-dark); /* –¢–µ–º–Ω—ã–π —Ñ–æ–Ω –∫–∞—Ä—Ç–æ—á–µ–∫ */
  border-color: var(--accent-dark); /* –¢–µ–º–Ω–∞—è –≥—Ä–∞–Ω–∏—Ü–∞ */
}

body.dark-theme .kanban-column.droppable-over {
