/* src/styles/AuthPage.css */

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –≤—Å–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã (—Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ) */
.login-page-container,
.register-page-container { /* –î–æ–±–∞–≤–∏–º –∫–ª–∞—Å—Å –∏ –¥–ª—è —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏ */
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 90vh; /* –ß—Ç–æ–±—ã –±–ª–æ–∫ –±—ã–ª –ø–æ —Ü–µ–Ω—Ç—Ä—É, –¥–∞–∂–µ –µ—Å–ª–∏ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ –º–∞–ª–æ */
    padding: 20px;
    background-color: var(--bg-primary); /* –ò—Å–ø–æ–ª—å–∑—É–µ–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é —Ç–µ–º—ã */
}

/* –°–∞–º –±–ª–æ–∫ —Ñ–æ—Ä–º—ã */
.auth-form-box { /* –ü–µ—Ä–µ–∏–º–µ–Ω–æ–≤–∞–ª –¥–ª—è –æ–±—â–Ω–æ—Å—Ç–∏ */
    background-color: var(--card-bg); /* –§–æ–Ω –∫–∞—Ä—Ç–æ—á–∫–∏ –∏–∑ —Ç–µ–º—ã */
    color: var(--text-primary);     /* –¢–µ–∫—Å—Ç –∏–∑ —Ç–µ–º—ã */
    padding: 30px 40px;
    border-radius: 8px;
    box-shadow: var(--shadow-lg);   /* –¢–µ–Ω—å –∏–∑ —Ç–µ–º—ã */
    width: 100%;
    max-width: 400px;               /* –û–≥—Ä–∞–Ω–∏—á–∏–≤–∞–µ–º –º–∞–∫—Å. —à–∏—Ä–∏–Ω—É */
    text-align: center;             /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –∑–∞–≥–æ–ª–æ–≤–æ–∫ –∏ —Ç–µ–∫—Å—Ç */
    transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease;
}

.auth-form-box h2 {
    margin-top: 0;
    margin-bottom: 25px;
    font-weight: 600;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –ø–æ–ª–µ–π —Ñ–æ—Ä–º—ã (–¥–æ–ø–æ–ª–Ω—è–µ–º –±–∞–∑–æ–≤—ã–µ –∏–∑ index.css) */
.auth-form-box .form-field {
    margin-bottom: 20px;
    text-align: left; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–µ–º label –∏ input –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */
}

.auth-form-box .form-field label {
    font-weight: 500;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–æ–∫ –≤–Ω—É—Ç—Ä–∏ —Ñ–æ—Ä–º—ã */
.auth-form-box .button--submit,
.auth-form-box .button--google {
    width: 100%; /* –ö–Ω–æ–ø–∫–∏ –Ω–∞ –≤—Å—é —à–∏—Ä–∏–Ω—É –±–ª–æ–∫–∞ */
    padding: 10px 15px;
    font-size: 1rem;
    font-weight: 500;
}

.auth-form-box .button--google {
    /* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–∫–∏ Google (—É–∂–µ –µ—Å—Ç—å –∏–Ω–ª–∞–π–Ω, –Ω–æ –º–æ–∂–Ω–æ –ø–µ—Ä–µ–Ω–µ—Å—Ç–∏) */
    /* display: flex; align-items: center; justify-content: center; gap: 10px; */
    /* –ú–æ–∂–Ω–æ –¥–æ–±–∞–≤–∏—Ç—å —Ä–∞–º–∫—É –∏–ª–∏ –∏–∑–º–µ–Ω–∏—Ç—å —Ñ–æ–Ω –¥–ª—è –∞–∫—Ü–µ–Ω—Ç–∞ */
    background-color: var(--card-bg); /* –§–æ–Ω –∫–∞–∫ —É –∫–∞—Ä—Ç–æ—á–∫–∏ */
    color: var(--text-primary); /* –¢–µ–∫—Å—Ç –∫–∞–∫ –æ—Å–Ω–æ–≤–Ω–æ–π */
    border: 1px solid var(--accent-color);
}
body.dark-theme .auth-form-box .button--google {
    /* –°—Ç–∏–ª–∏ –∫–Ω–æ–ø–∫–∏ Google –≤ —Ç–µ–º–Ω–æ–π —Ç–µ–º–µ, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ –æ—Ç–ª–∏—á–∏–µ */
    border-color: var(--text-secondary);
}
.auth-form-box .button--google:hover:not(:disabled) {
    background-color: var(--bg-secondary); /* –õ–µ–≥–∫–æ–µ –∏–∑–º–µ–Ω–µ–Ω–∏–µ —Ñ–æ–Ω–∞ –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
}
.auth-form-box .button--google svg {
    margin-right: 8px; /* –û—Ç—Å—Ç—É–ø –¥–ª—è –∏–∫–æ–Ω–∫–∏ */
}


/* –†–∞–∑–¥–µ–ª–∏—Ç–µ–ª—å "–ò–õ–ò" */
.divider {
    text-align: center;
    margin: 25px 0;
    color: var(--text-secondary);
    font-size: 0.9em;
    position: relative;
    /* –õ–∏–Ω–∏–∏ –ø–æ –±–æ–∫–∞–º (–æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ) */
    display: flex;
    align-items: center;
}
.divider::before,
.divider::after {
    content: '';
    flex-grow: 1;
    height: 1px;
    background-color: var(--accent-color);
    margin: 0 10px;
    transition: background-color var(--transition-speed) ease;
}


/* –°—Å—ã–ª–∫–∏ –≤–Ω–∏–∑—É */
.auth-form-box p {
    margin-top: 20px;
    margin-bottom: 5px;
    font-size: 0.9em;
    color: var(--text-secondary);
}
.auth-form-box p a {
    color: var(--text-primary); /* –°—Å—ã–ª–∫–∞ –æ—Å–Ω–æ–≤–Ω—ã–º —Ü–≤–µ—Ç–æ–º —Ç–µ–∫—Å—Ç–∞ */
    font-weight: 500;
    text-decoration: none;
}
.auth-form-box p a:hover {
    text-decoration: underline;
}

/* –°–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ */
.auth-form-box .error-message {
    text-align: left; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ –æ—à–∏–±–∫–∏ */
    font-size: 0.9em;
    margin-bottom: 15px;
}