/* src/styles/ContactPage.css */

/* –û–±—â–∏–π –∫–ª–∞—Å—Å page-container –ø—Ä–µ–¥–ø–æ–ª–∞–≥–∞–µ—Ç—Å—è –≤ index.css/App.css */
/* .page-container { max-width: 1500px; margin: 0 auto; padding: 0 20px; } */

.contact-page h1 {
    text-align: center;
    margin-bottom: 40px; /* –£–≤–µ–ª–∏—á–∏–ª–∏ –æ—Ç—Å—Ç—É–ø */
    color: var(--text-primary); /* –Ø–≤–Ω—ã–π —Ü–≤–µ—Ç */
    transition: color var(--transition-speed) ease;
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –¥–≤—É—Ö –∫–æ–ª–æ–Ω–æ–∫ */
.contact-page__content {
    display: flex;
    flex-wrap: wrap; /* –ü–µ—Ä–µ–Ω–æ—Å –Ω–∞ –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–∞—Ö */
    gap: 30px; /* –ü—Ä–æ—Å—Ç—Ä–∞–Ω—Å—Ç–≤–æ –º–µ–∂–¥—É –∫–æ–ª–æ–Ω–∫–∞–º–∏ */
    margin-top: 30px;
    /* align-items: flex-start; */ /* –£–±—Ä–∞–ª–∏, —á—Ç–æ–±—ã –∫–æ–ª–æ–Ω–∫–∏ —Ä–∞—Å—Ç—è–≥–∏–≤–∞–ª–∏—Å—å –ø–æ –≤—ã—Å–æ—Ç–µ (–ø–æ —É–º–æ–ª—á–∞–Ω–∏—é stretch) */
    justify-content: center;
}

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–æ–ª–æ–Ω–æ–∫ */
.contact-page__column {
    flex: 1;
    min-width: 320px;
    /* max-width: calc(50% - 15px); */ /* –£–±—Ä–∞–ª–∏ –º–∞–∫—Å. —à–∏—Ä–∏–Ω—É, –ø—É—Å—Ç—å —Ä–∞—Å—Ç—è–≥–∏–≤–∞—é—Ç—Å—è */
    display: flex; /* –î–ª—è —Ä–∞—Å—Ç—è–≥–∏–≤–∞–Ω–∏—è –¥–æ—á–µ—Ä–Ω–µ–≥–æ .auth-form-box */
}

/* –ü–µ—Ä–µ–æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ —Å—Ç–∏–ª–µ–π –¥–ª—è .auth-form-box –í–ù–£–¢–†–ò –ö–û–õ–û–ù–û–ö */
.contact-page__column .auth-form-box {
    width: 100%;      /* –ó–∞–ø–æ–ª–Ω—è–µ—Ç —Å–≤–æ—é –∫–æ–ª–æ–Ω–∫—É */
    max-width: none;  /* –£–±–∏—Ä–∞–µ–º –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ —à–∏—Ä–∏–Ω—ã */
    margin: 0;        /* –£–±–∏—Ä–∞–µ–º –≤–Ω–µ—à–Ω–∏–µ –æ—Ç—Å—Ç—É–ø—ã */
    text-align: left; /* –¢–µ–∫—Å—Ç –≤–Ω—É—Ç—Ä–∏ –±–æ–∫—Å–∞ - —Å–ª–µ–≤–∞ */
    padding: 25px 30px; /* –í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–π –æ—Ç—Å—Ç—É–ø */
    height: 100%;     /* <<< –†–∞—Å—Ç—è–≥–∏–≤–∞–µ–º –±–æ–∫—Å –ø–æ –≤—ã—Å–æ—Ç–µ –∫–æ–ª–æ–Ω–∫–∏ */
    display: flex;
    flex-direction: column; /* –í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ —ç–ª–µ–º–µ–Ω—Ç—ã –∏–¥—É—Ç –∫–æ–ª–æ–Ω–∫–æ–π */
    background-color: var(--card-bg); /* –§–æ–Ω */
    border: 1px solid var(--accent-color); /* –†–∞–º–∫–∞ */
    border-radius: 8px; /* –°–∫—Ä—É–≥–ª–µ–Ω–∏–µ */
    box-shadow: var(--shadow-md); /* –¢–µ–Ω—å */
    transition: background-color var(--transition-speed) ease, border-color var(--transition-speed) ease;
}

/* –ó–∞–≥–æ–ª–æ–≤–∫–∏ –≤–Ω—É—Ç—Ä–∏ –±–æ–∫—Å–æ–≤ */
.contact-page__column .auth-form-box h2 {
    text-align: center; /* –ó–∞–≥–æ–ª–æ–≤–æ–∫ –ø–æ —Ü–µ–Ω—Ç—Ä—É */
    margin-top: 0;
    margin-bottom: 25px;
    padding-bottom: 10px;
    border-bottom: 1px solid var(--accent-color);
    font-size: 1.3em;
    font-weight: 600;
    flex-shrink: 0; /* –ù–µ —Å–∂–∏–º–∞—Ç—å—Å—è */
    transition: border-color var(--transition-speed) ease;
}
/* –ê–±–∑–∞—Ü—ã –≤–Ω—É—Ç—Ä–∏ –±–æ–∫—Å–æ–≤ */
.contact-page__column .auth-form-box p {
    text-align: left; /* –¢–µ–∫—Å—Ç —Å–ª–µ–≤–∞ */
    margin-bottom: 20px;
    color: var(--text-secondary);
    flex-shrink: 0;
}

/* –§–æ—Ä–º–∞ –≤–Ω—É—Ç—Ä–∏ –±–æ–∫—Å–∞ (—Ä–∞—Å—Ç—è–≥–∏–≤–∞–µ—Ç—Å—è) */
.contact-page__column .auth-form-box form {
    display: flex;
    flex-direction: column;
    flex-grow: 1; /* –ó–∞–Ω–∏–º–∞–µ—Ç –æ—Å—Ç–∞–≤—à–µ–µ—Å—è –º–µ—Å—Ç–æ */
}
.contact-page__column .auth-form-box form .form-field {
    margin-bottom: 15px; /* –û—Ç—Å—Ç—É–ø –º–µ–∂–¥—É –ø–æ–ª—è–º–∏ */
    flex-shrink: 0;
}
.contact-page__column .auth-form-box form .form-field label {
    text-align: left;
    display: block;
    margin-bottom: 5px;
    color: var(--text-secondary);
    font-size: 0.9em;
    font-weight: 500;
}
/* –°—Ç–∏–ª—å –¥–ª—è textarea */
.contact-page__column .auth-form-box form textarea {
    flex-grow: 1; /* –ü–æ–ª–µ –æ–ø–∏—Å–∞–Ω–∏—è –æ—à–∏–±–∫–∏ —Ä–∞—Å—Ç—è–≥–∏–≤–∞–µ—Ç—Å—è */
    min-height: 100px; /* –ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è –≤—ã—Å–æ—Ç–∞ */
}


/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–Ω–æ–ø–∫–∏ Submit (–ø—Ä–∏–∂–∏–º–∞–µ–º –∫ –Ω–∏–∑—É) */
.contact-page__column .auth-form-box .form-actions {
    margin-top: auto; /* –ü—Ä–∏–∂–∏–º–∞–µ—Ç –∫–Ω–æ–ø–∫—É –∫ –Ω–∏–∑—É —Ñ–æ—Ä–º—ã */
    padding-top: 20px;
    text-align: right; /* –ö–Ω–æ–ø–∫–∞ —Å–ø—Ä–∞–≤–∞ */
    flex-shrink: 0;
}

/* –ö–Ω–æ–ø–∫–∞ "–û—Ç–ø—Ä–∞–≤–∏—Ç—å –æ—Ç—á–µ—Ç" */
.contact-page__column--left .auth-form-box .button--submit {
    background-color: var(--card-bg);
    color: var(--text-primary);
    border: 1px solid var(--accent-color);
    font-weight: 500;
    min-width: 160px;
    width: auto; /* –£–±—Ä–∞–ª–∏ display: block, —á—Ç–æ–±—ã text-align —Å—Ä–∞–±–æ—Ç–∞–ª */
    padding: 8px 15px; /* –í–µ—Ä–Ω—É–ª–∏ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–π –ø–∞–¥–¥–∏–Ω–≥ */
    transition: background-color var(--transition-speed) ease, color var(--transition-speed) ease, border-color var(--transition-speed) ease;
}
/* –•–æ–≤–µ—Ä –¥–ª—è –∫–Ω–æ–ø–∫–∏ "–û—Ç–ø—Ä–∞–≤–∏—Ç—å –æ—Ç—á–µ—Ç" */
.contact-page__column--left .auth-form-box .button--submit:hover:not(:disabled) {
    /* –°–≤–µ—Ç–ª–∞—è —Ç–µ–º–∞: –§–æ–Ω —á—É—Ç—å —Ç–µ–º–Ω–µ–µ */
    background-color: color-mix(in srgb, var(--card-bg) 85%, black);
    border-color: color-mix(in srgb, var(--text-primary) 40%, transparent);
    color: var(--text-primary);
    opacity: 1;
}
/* –•–æ–≤–µ—Ä –¥–ª—è –∫–Ω–æ–ø–∫–∏ –≤ –¢–ï–ú–ù–û–ô –¢–ï–ú–ï */
body.dark-theme .contact-page__column--left .auth-form-box .button--submit:hover:not(:disabled) {
    background-color: color-mix(in srgb, var(--card-bg) 85%, white);
    border-color: color-mix(in srgb, var(--text-primary) 60%, transparent);
}

/* ReadOnly –ø–æ–ª–µ Email */
input[readOnly] {
    background-color: var(--bg-secondary);
    cursor: not-allowed;
    opacity: 0.7; /* –ß—É—Ç—å –±–ª–µ–¥–Ω–µ–µ */
    transition: background-color var(--transition-speed) ease;
}

/* --- –°—Ç–∏–ª–∏ –¥–ª—è Flip Card --- */
.flip-card-container {
    display: flex;
    justify-content: center;
    margin-top: 25px;
}
.flip-card {
    background-color: transparent;
    width: 300px; height: 188px;
    perspective: 1000px;
    cursor: pointer;
}
.flip-card-inner {
    position: relative; width: 100%; height: 100%;
    text-align: center; transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: var(--shadow-md); border-radius: 10px;
}
.flip-card:hover .flip-card-inner { transform: rotateY(180deg); }
.flip-card-front, .flip-card-back {
    position: absolute; width: 100%; height: 100%;
    backface-visibility: hidden; -webkit-backface-visibility: hidden;
    border-radius: 10px; display: flex; flex-direction: column;
    justify-content: space-between; padding: 20px;
    color: var(--text-primary); overflow: hidden;
    border: 1px solid var(--accent-color);
    transition: background var(--transition-speed) ease, border-color var(--transition-speed) ease, color var(--transition-speed) ease;
}
.flip-card-front { background: linear-gradient(45deg, var(--bg-secondary), color-mix(in srgb, var(--bg-secondary) 85%, black)); }
body.dark-theme .flip-card-front { background: linear-gradient(45deg, var(--bg-secondary), color-mix(in srgb, var(--bg-secondary) 85%, white)); }
.flip-card-logo { font-size: 1.2em; font-weight: bold; text-align: right; opacity: 0.8; }
.flip-card-chip { width: 40px; height: 30px; background-color: #e0e0e0; border-radius: 4px; align-self: flex-start; border: 1px solid #bdbdbd; }
body.dark-theme .flip-card-chip { background-color: #757575; border-color: #9e9e9e; }
.flip-card-hint { font-size: 0.9em; font-weight: 500; text-align: center; align-self: center; opacity: 0.9; }
.flip-card-back { background-color: var(--bg-secondary); transform: rotateY(180deg); justify-content: center; align-items: center; font-family: 'Courier New', Courier, monospace; }
.flip-card-back-header { font-size: 0.8em; margin-bottom: 15px; color: var(--text-secondary); }
.flip-card-number { font-size: 1.4em; letter-spacing: 2px; font-weight: 600; margin-bottom: 10px; }
.flip-card-recipient { font-size: 0.9em; color: var(--text-secondary); }